.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.mt-10 {
  padding-top: 3vw;
}
.mb-10 {
  padding-bottom: 5vw;
}
.ml-10 {
  padding-left: 5vw;
}
.mr-10 {
  padding-right: 5vw;
}
.img-size{
  height: 30vw;
  width: 40vw;
}
.DropZone{
  border: 3px dashed #888;
  border-radius: 4px;
  color: #aaa;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  padding: 5em;
  cursor: pointer;
  display: flex;
}
.error{
  color: red;
}
.navBtn{
  background-color: transparent;
  border: none;
  outline-color: transparent;
  padding: 0;
  padding-top: 0.2vw;
  text-align: center;
}
.nameText{
  padding-top: 0.4vw;
  color: yellow;
}

@media only screen and (max-width: 600px) {
  .DropZone{
    border: 3px dashed #888;
    border-radius: 4px;
    color: #aaa;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    padding: 2em;
    cursor: pointer;
    display: flex;
    margin-bottom: 2vw;
  }
}